.page {
  width: 100%;
  min-width: 320px;
  height: 100%;
  display: block;
  background:
    fixed url("/../img/space-star-wallpaper.png") repeat center,
    url("/../img/mars-kosmos-korabl.jpg") no-repeat center 337px,
    fixed url("/../img/background-pattern.jpg") repeat;
  background-size: 400%;

  @media (#{$screen-tablet}) {
    min-width: 768px;
    background:
      fixed url("/../img/space-star-wallpaper.png") repeat center,
      fixed url("/../img/mars-kosmos-korabl.jpg") no-repeat center 175px,
      fixed url("/../img/background-pattern.jpg") repeat;
    background-size: auto;
  }

  @media (#{$screen-desktop}) {
    min-width: 1440px;
    max-width: 1920px;
    background:
      fixed url("/../img/space-star-wallpaper.png") repeat center,
      fixed url("/../img/mars-kosmos-korabl.jpg") no-repeat center 145px,
      fixed url("/../img/background-pattern.jpg") repeat;
    background-size: auto;
  }
}
