.logo {
  display: flex;
  position: relative;
  width: calc(100% + 40px);
  height: 70px;
  justify-content: center;
  border: 1px solid $basic-border;
  border-top: none;
  margin: 0 -20px;
  padding: 0 15px 0 60px;
  align-items: center;

  @media (#{$screen-tablet}) {
    width: calc(100% + 60px);
    margin: 0 -30px;
  }

  @media (#{$screen-desktop}) {
    display: flex;
    position: relative;
    width: 315px;
    height: 100px;
    justify-content: center;
    padding: 25px 15px 35px 60px;
    border-bottom: none;
    margin: 0 auto 0 0;
  }

  &:hover img,
  &:focus img {
    opacity: .5;
  }

  &:hover .logo__corner--1:before,
  &:hover .logo__corner--2:before,
  &:hover .logo__corner--1:after,
  &:hover .logo__corner--2:after,
  &:focus .logo__corner--1:before,
  &:focus .logo__corner--2:before,
  &:focus .logo__corner--1:after,
  &:focus .logo__corner--2:after {
    width: 10px;
    height: 10px;
  }
}

.logo__corner--1 {
  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    background: transparent;
    border: 1px solid $basic-white;
    border-bottom: none;
    border-right: none;
    top: -1px;
    left: -1px;
  }

  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    background: transparent;
    border: 1px solid $basic-white;
    border-bottom: none;
    border-left: none;
    top: -1px;
    right: -1px;
  }
}

.logo__corner--2 {
  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    background: transparent;
    border: 1px solid $basic-white;
    border-top: none;
    border-right: none;
    bottom: -1px;
    left: -1px;
  }

  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    background: transparent;
    border: 1px solid $basic-white;
    border-top: none;
    border-left: none;
    bottom: -1px;
    right: -1px;
  }
}

.logo__img {
  width: 274px;
  height: 36px;
}
