.header {
  width: calc(100% + 40px);
  display: block;
  background-color: $header-background;
  color: $basic-white;
  align-items: center;
  border-top: 1px solid $basic-border;
  border-bottom: 1px solid $basic-border;
  margin: 0 -20px;
  padding: 0 20px;

  @media (#{$screen-tablet}) {
    width: calc(100% + 60px);
    margin: 0 -30px;
    padding: 0 30px;
    display: block;
  }

  @media (#{$screen-desktop}) {
    width: calc(100% + 220px);
    margin: 0 -110px;
    padding: 0 110px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;
  }

  @media (#{$screen-4k}) {
    width: calc(100% + 860px);
    margin: 0 -430px;
    padding: 0 430px;
  }
}
